
.c_user_center .ucm_title_lt {
    float: left;
}
.c_user_center .ucm_title .ucm_title_lt a {
    display: inline-block;
    float: none;
    border: none;
    color: #000;
    font-size: 14px;
    font-weight: 600;
    text-align: left;
}
.c_user_center .ucm_title .ucm_title_rt a {
    width: 70px;
    color: #fff;
    border: none;
    margin-left: 10px;
    border-radius: 2px;
}
.c_user_center .in_m {
    background-color: #ff7200;
}
.c_user_center .ot_m {
    background-color: #3aa9f2;
}
.c_user_center .ucm_title {
    margin-bottom: 30px !important;
}

/* 个人信息 */
.c_user_center .my_info {
    height: 60px;
}
.c_user_center .my_info .my_photo {
    width: 60px;
    height: 60px;
    float: left;
    overflow: hidden;
}
.c_user_center .my_info .my_photo img {
    width: 60px;
    height: 60px;
    border-radius: 100%;
}
.c_user_center .my_info .my_name {
    width: 650px;
    line-height: 60px;
    float: left;
    margin-left: 20px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

/* 我的资金 */
.c_user_center .uc_item {
    padding: 30px 0;
    border-bottom: 1px solid #e0e0e0;
}
.c_user_center .uc_item dt {
    margin-bottom: 50px;
    font-size: 14px;
    font-weight: 600;
}
.c_user_center .my_money dd > div {
    display: inline-block;
    width: 30%;
}
.c_user_center .my_money dd h3 {
    color: #777;
    margin-bottom: 10px;
}
.c_user_center .my_money dd p {
    color: #f02d2d;
}
.uc_item .more {
    float: right;
    color: #999;
    font-size: 12px;
    font-weight: normal;
}
.uc_item a:hover {
    color: #3aa9f2;
}
.uc_item .row {
    height: 30px;
    line-height: 30px;
    margin-bottom: 25px;
}
.uc_item .row:last-child {
    margin-bottom: 0;
}
.uc_item .row span {
    display: block;
    float: left;
    text-align: left;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.uc_item .row .cell1 {
    width: 160px;
}
.uc_item .row .cell2 {
    width: 110px;
    margin: 0 15px;
}
.uc_item .row .cell3 {
    width: 110px;
}
.uc_item .row .cell4 {
    width: 110px;
}
.uc_item .row .cell5 {
    width: 110px;
}
.uc_item .row .cell6 {
    width: 120px;
    text-align: right;
}
.uc_item .figure {
    width: 120px;
    float: left;
    margin: 0 15px 20px 0;
}
.uc_item .figure img {
    width: 100%;
    height: 80px;
}
.uc_item .figure h3 {
    color: #555;
    font-size: 12px;
    margin: 8px 0 2px;
}
.uc_item .figure p {
    color: #999;
    font-size: 12px;
}
.uc_item .figure p i {
    color: #f02d2d;
}
.uc_item.my_focus {
    padding-bottom: 10px;
}
.uc_item.my_foot {
    padding-bottom: 0;
    border-bottom: none;
}



