
.c_quote_sidebar{
    padding-top: 1px;
    background-color: #fff;
}
.c_quote_sidebar a{
    display: block;
    height: 45px;
    line-height: 45px;
    text-indent: 10px;
    margin-top: -1px;
    color: #006c05;
    font-size: 16px;
    font-weight: 600;
    border: 1px solid #e0e0e0;
}
.c_quote_sidebar a.active,
.c_quote_sidebar a:hover{
    color: #fff;
    background-color: #006c05;
}



