
.c_tea_news .ns_top{
    position: relative;
    height: 230px;
}
.c_tea_news .ns_top img{
    height: 100%;
}
.c_tea_news .ns_top_text {
    position: absolute;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 30px;
    line-height: 30px;
    color: #fff;
    font-size: 12px;
    padding: 0 5px;
    box-sizing: border-box;
    background: url("../../../assets/images/opacity.png");
}
.c_tea_news .ns_top_text a{
    color: #fff;
    float: right;
}
.c_tea_news .ns_list {
    margin-top: 8px;
}
.c_tea_news .ns_list a{
    display: block;
    font-size: 12px;
}



