
.c_tea_deal .dl_top{
    height: 45px;
    line-height: 45px;
}
.c_tea_deal .dl_main{
    height: 255px;
    overflow: hidden;
    background-color: #ebf5ff;
}
.c_tea_deal .dl_main .dl_head{
    height: 30px;
    height: 30px;
    color: #fff;
    font-size: 12px;
    background-color: #3f8fd8;
}
.c_tea_deal .dl_main .cell {
    display: block;
    width: 33.3%;
    height: 30px;
    line-height: 30px;
    text-align: center;
    float: left;
    font-size: 12px;
    white-space: normal;
    text-overflow: ellipsis;
    overflow: hidden;
}
.c_tea_deal .dl_main .dl_body{
    height: 220px;
    padding-top: 10px;
    overflow: hidden;
}
.c_tea_deal .dl_main .dl_body ul li{
    height: 30px;
}



