
@import "../../assets/css/uc/uc_form.css";
.c_card_manage .common_form{
    padding-left: 0;
    margin-top: 20px;
}
.c_card_manage .fm_item label{
    width: 120px !important;
}
.c_card_manage .fm_item input{
    width: 200px !important;
}
.c_card_manage .g_btn{
    margin-left: 120px;
}
.c_card_manage .uc_main .ucm_title{
    margin-bottom: 25px;
}
.c_card_manage .tip{
    margin-bottom: 10px;
}
.c_card_manage .not_auth{
    color: #ff0000;
    padding: 20px 0;
}
/* 已认证 */
.has_auth .card_info{
    border: 2px solid #3aa9f2;
}
.has_auth .card_info .row {
    height: 60px;
    line-height: 60px;
}
.has_auth .card_info .row.hdr{
    height: 40px;
    line-height: 40px;
    color: #fff;
    background-color: #3aa9f2;
}
.has_auth .card_info .cell{
    display: block;
    width: 33.3%;
    float: left;
    text-align: center;
}
.has_auth .card_info .modify{
    margin-left: 20px;
    color: #3aa9f2;
}
/* 绑卡 */
.bind_card .banks {
    width: 772px;
    font-size: 0;
}
.bind_card .banks img{
    display: inline-block;
    width: 181px;
    height: 37px;
    margin: 0 12px 15px 0;
    border: 1px solid #ccc;
}



