













































































/* 实时滚动信息 */
.loop_scroll {
    height: 50px;
    line-height: 50px;
    color: #fff;
    overflow: hidden;
    background-color: #115020;
}
.loop_scroll ul {
    width: auto;
    font-size: 0;
    float: left;
    white-space: nowrap;
}
.loop_scroll ul li{
    display: inline-block;
    padding-right: 10px;
    font-size: 14px;
}
.loop_scroll .gn{
    color: #07ed00;
}
.loop_scroll .rd{
    color: #ff0000;
}



