
@import '../../assets/css/home/ui_list.css';
@import '../../assets/css/common/position_bar.css';
@import '../../assets/css/hall/hall.css';
.c_quote .lty_02{
    padding: 20px 25px;
    box-sizing: border-box;
}
.c_quote .lty_02 .lty_02_top{
    padding-bottom: 20px;
    border-bottom: 1px solid #e0e0e0;
}
.c_quote .lty_02_top h3{
    font-size: 24px;
    margin-bottom: 5px;
}
.c_quote .lty_02_top p{
    color: #888;
    font-size: 12px;
}
.c_quote .lty_02_top p span{
    margin-right: 60px;
}
.c_quote .lty_02 .lty_02_ctr{
    padding: 30px 0;
    border-bottom: 1px solid #e0e0e0;
}
.c_quote .info_title {
    font-size: 18px;
    margin-bottom: 30px;
}
.c_quote .info_detail p{
    color: #555;
    margin-bottom: 15px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.c_quote .info_detail p span{
    margin-right: 30px;
}
.c_quote .info_detail input{
    width: 100px;
    height: 20px;
    line-height: 20px;
    border: 1px solid #888;
    border-radius: 2px;
}
.c_quote .info_dtl_lt{
    width: 180px;
    float: left;
    margin-right: 10px;
}
.c_quote .info_dtl_cr{
    width: 280px;
    float: left;
}
.c_quote .info_dtl_rt{
    width: 400px;
    float: left;
}
.c_quote .lty_02_bom{
    text-align: center;
    padding: 30px 0 300px;
}
.c_quote .lty_02_bom .g_btn{
    color: #fff;
    font-size: 16px;
    padding: 5px 20px;
    cursor: pointer;
    background-color: #f02d2d;
}
.c_quote .help.is-danger{
    position: relative;
    top: -1px;
    color: #f02d2d;
    font-size: 12px;
}



