
.g_top {
    height: 40px;
    line-height: 40px;
    background-color: #44a84b
}
.g_top a {
    color: #fff
}
.g_top .top_lt, .g_top .top_rt {
    width: 50%;
    float: left
}
.g_top .top_rt {
    text-align: right
}
.g_top .top_rt a {
    margin-left: 25px
}
.g_header {
    height: 125px
}
.g_header .header_logo {
    float: left
}
.g_header .header_logo .logo_link {
    display: block;
    width: 468px;
    height: 122px;
    background: url("../assets/images/logo.jpg") no-repeat
}
.g_header .header_search {
    float: right;
    margin-top: 44px
}
.g_header .search_main {
    width: 470px;
    height: 36px
}
.g_header .header_search .search_box {
    width: 386px;
    height: 36px;
    float: left;
    padding-left: 10px;
    border: 2px solid #44a84b
}
.g_header .header_search input {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border: none;
    width: 100%;
    margin-top: 5px
}
.g_header .header_search .search_btn {
    width: 70px;
    height: 36px;
    line-height: 36px;
    text-align: center;
    color: #fff;
    float: left;
    background-color: #44a84b
}
.g_nav {
    height: 50px;
    line-height: 50px;
    background-color: #e5e5e5
}
.g_nav ul li {
    float: left
}
.g_nav ul li a {
    display: block;
    color: #0c582a;
    font-size: 16px;
    font-weight: 600;
    padding: 0 40px;
    border-right: 1px solid #fff;
}
.g_nav ul li:last-child a {
    border-right: none
}
.g_nav ul li a.active, .g_nav ul li a:hover {
    color: #fff;
    background-color: #0c582a
}



