
.c_capital_detail .cpl_dtl_top {
    height: 24px;
    overflow: hidden;
}
.c_capital_detail .cpl_dtl_top > div{
    line-height: 24px;
    float: left;
    margin-right: 50px;
}
.c_capital_detail .cpl_dtl_top label{
    display: block;
    float: left;
}
.c_capital_detail .ivu-date-picker {
    float: left !important;
}
.c_capital_detail .ivu-input {
    outline: none;
    border-color: #ccc !important;
    border-radius: 0 !important;
}
.c_capital_detail select {
    width: 130px;
    height: 24px;
    float: left;
    color: #657180;
    font-size: 12px;
    padding-left: 5px;
    border: 1px solid #ccc;
}
.c_capital_detail .search{
    display: block;
    width: 80px;
    height: 24px;
    text-align: center;
    color: #fff;
    border-radius: 3px;
    background-color: #ff7200;
}
/* 收支明细列表 */
.cpl_dtl_tab{
    padding-top: 30px;
}
.cpl_dtl_tab .row {
    width: 100%;
    height: 45px;
    line-height: 45px;
    margin-top: -1px;
    overflow: hidden;
    border: 1px solid #e0e0e0;
}
.cpl_dtl_tab .row.hdr span{
    color: #333;
    font-size: 14px;
}
.cpl_dtl_tab .row span{
    display: block;
    float: left;
    color: #555;
    font-size: 12px;
    text-align: center;
    white-space: normal;
    text-overflow: ellipsis;
    overflow: hidden;
}
.cpl_dtl_tab .row .cell1{
   width: 5%;
}
.cpl_dtl_tab .row .cell2{
    width: 5%;
}
.cpl_dtl_tab .row .cell3{
    width: 15%;
}
.cpl_dtl_tab .row .cell4{
    width: 15%;
}
.cpl_dtl_tab .row .cell5{
    width: 15%;
}
.cpl_dtl_tab .row .cell6{
    width: 15%;
}
.cpl_dtl_tab .row .cell7{
    width: 15%;
}
.cpl_dtl_tab .row .cell8{
    width: 15%;
}



