
.c_guide .lty_container {
  padding: 120px 0 200px;
}
.c_guide .guide_box {
  width: 530px;
  height: 390px;
  margin: 0 auto;
  padding: 50px 80px;
  text-align: center;
  background-color: #fff;
}
.c_guide .guide_box_top {
  height: 40px;
  line-height: 40px;
  margin-bottom: 60px;
}
.c_guide .guide_box_top i {
  position: relative;
  left: 90px;
  display: block;
  width: 38px;
  height: 38px;
  float: left;
  background: url("../../assets/images/index_icons.png") no-repeat;
  background-position: -457px -144px;
}
.c_guide .guide_box_ctr {
  height: 40px;
  margin-bottom: 30px;
}
.c_guide .guide_box_ctr a {
  display: block;
  width: 175px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #fff;
  font-size: 14px;
  border-radius: 2px;
}
.c_guide .guide_box_ctr .go_auth {
  float: left;
  background-color: #ff7200;
}
.c_guide .guide_box_ctr .go_before {
  float: right;
  color: #3aa9f2;
  border: 1px solid #3aa9f2;
}
.c_guide .guide_box_bom {
  text-align: left;
  color: #f02d2d;
  font-size: 12px;
}



