
@import "../../assets/css/hall/lty_order_box.css";
.c_order .lty_container{
    padding: 20px 0;
}
.c_order .long_btn {
    display: inline-block;
    width: 150px;
    height: 25px;
    line-height: 25px;
    color: #666;
    text-align: center;
    cursor: pointer;
    border: 1px solid #ddd;
}
.c_order .box_bom .btn_add{
    display: block;
    margin: 0 auto;
}
.c_order .box_bom p{
    margin-bottom: 7px;
}
.c_order .box_bom_lt{
    width: 400px;
    float: left;
    margin-left: 130px;
}
.c_order .box_bom_rt{
    width: 300px;
    float: right;
    text-align: right;
}
.c_order .box_bom_rt .btns{
    text-align: right;
    margin-bottom: 15px;
}
.c_order .box_bom_rt .btns span{
    display: inline-block;
    padding: 2px 20px;
    color: #fff;
    cursor: pointer;
    -moz-user-select: none;
     -ms-user-select: none;
         user-select: none;
    -webkit-user-select: none;
}
.c_order .box_bom_rt .btn_edit{
    background-color: #ff7200;
}
.c_order .box_bom_rt .btn_dele{
    margin-left: 10px;
    background-color: #3aa9f2;
}
/* modal */
.vertical-center-modal{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.vertical-center-modal .ivu-modal{
    top: 0;
}
/* modal-form */
.fm_uinfo .fm_item{
    height: 30px;
    overflow: hidden;
}
.fm_uinfo .fm_item label{
    display: block;
    width: 100px;
    line-height: 30px;
    text-align: right;
    float: left;
    margin-right: 20px;
}
.fm_uinfo .fm_item input,
.fm_uinfo .fm_item select{
    width: 300px;
    height: 30px;
    line-height: 30px;
    float: left;
    border: 1px solid #e0e0e0;
}
.fm_uinfo .fm_error{
    width: 300px;
    height: 20px;
    line-height: 15px;
    margin-left: 120px;
    font-size: 12px;
}
.fm_uinfo .fm_error span{
    float: left;
    color: #ff0000;
}



