
.c_count_down .bs_time {
    float: right;
    margin-left: 30px;
}
.c_count_down .bs_time span {
    position: relative;
    top: -1px;
    display: inline-block;
    line-height: normal;
    padding: 0 5px;
    color: #fff;
    font-size: 12px;
    margin: 0 5px;
    background-color: #000;
}
.c_count_down .jy_time {
    position: relative;
    float: right;
}
.c_count_down .jy_time:after {
    position: absolute;
    top: 18px;
    right: -16px;
    display: block;
    width: 1px;
    height: 14px;
    content: "";
    background-color: #999;
}



