
.uc_table {
    width: 100%;
}
.uc_table thead {
    height: 50px;
    line-height: 50px;
    background-color: #3aa9f2;
}
.uc_table thead th{
    color: #fff;
    text-align: center;
}
.uc_table tbody tr{
    border: 1px solid #e0e0e0;
}
.uc_table tbody td{
    height: 80px;
    line-height: 80px;
    text-align: center;
    padding: 10px 0;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.uc_table tbody td img{
    display: block;
    width: 100px;
    height: 80px;
    float: left;
}
.uc_table tbody td a{
    margin-right: 5px;
}
.uc_table tbody td a:hover{
    color: #3aa9f2;
}



