
.c_small_slider .swiper-slide{
    position: relative;
    height: 255px;
}
.c_small_slider .swiper-slide img{
    width: 100%;
    height: 100%;
}
.c_small_slider .swiper-slide .title{
    position: absolute;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 30px;
    line-height: 30px;
    text-align: center;
    color: #fff;
    background: url("../../../assets/images/opacity.png");
}




